@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
* {
  margin: 0;
  font-family: "Montserrat", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}
body {
  --twitter-color: #2ba8f7;
  --twitter-hover: #202e3a;
  --twitter-background: rgb(21, 32, 43);
  --twitter-border: rgb(51, 66, 78);
  background-color: var(--twitter-background);
  -ms-scroll-chaining: none;
      overscroll-behavior: contain;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.landing {
  position: relative;
  background: url(/static/media/twitter-social-media-emoji-3d-render-background-social-media-balloon-symbol_41204-1885.4a70436a.jpg)
    no-repeat center center/cover;
  height: 100vh;
}
.buttons {
  /* width: 36%; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.landing-inner {
  color: white;
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.parent__link{
  text-decoration: none;
}
.dark-overlay {
  background-color: rgba(0, 0, 0, 0.85);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.lead {
  margin-top: 10px;
  font-size: 1.5rem;
}
.x-large {
  font-size: 3.5rem;
}
.t__span {
  color: var(--twitter-color);
}
