.landing__login {
  padding: 10px;
  background-color: var(--twitter-color);
  min-width: 200px;
  margin-top: 15px;
  cursor: pointer;
  border-radius: 20px;
}

.landing__register {
  padding: 10px;
  background-color: white;
  min-width: 200px;
  margin-top: 15px;
  cursor: pointer;
  border-radius: 20px;
}

.landing__login__link {
  text-decoration: none !important;
  font-size: large;
  font-weight: 600;
  color: white;
}
.landing__register__link {
  text-decoration: none !important;
  font-size: large;
  font-weight: 600;
  color: var(--twitter-color);
}

.sidebarOption {
  display: flex;
  align-items: center;
  cursor: pointer;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding-right: 15px;
  margin-bottom: 5px;
}
.sidebarOption > .MuiSvgIcon-root {
  padding: 10px;
  font-size: 55px;
}
.sidebarOption > h2 {
  font-weight: 600;
  font-size: 18px;
}
.sidebarOption:hover {
  background-color: var(--twitter-hover);
  border-radius: 30px;
  color: var(--twitter-color);
  transition: color 100ms ease-in-out;
}
.sidebarOption--active {
  color: var(--twitter-color);
}
@media only screen and (max-width: 1280px) {
  .sidebarOption > h2 {
    display: none;
  }
  .sidebarOption {
    padding: 0;
  }
}

.MuiDialog-paper {
  background-color: var(--twitter-background) !important;
  color: white !important;
}
.MuiDialogContent-dividers {
  border: none !important;
}
.login__link__modal {
  font-size: 20px;
  font-weight: 500;
  color: var(--twitter-color);
  text-decoration: none !important;
}
.login__link__modal__div {
  margin-bottom: 50px;
}
.login__link__modal:hover {
  color: rgb(64, 196, 240);
  text-decoration: none !important;
}
.logout__modal__main {
  display: flex;
  flex-direction: column;
}
.logout__text {
  text-align: center;
  padding: 15px;
}
.logout__icon {
  text-align: center;
}
.logout__icon > .MuiSvgIcon-root {
  font-size: 60px;
  color: var(--twitter-color);
}
.logout__text__p {
  margin-top: 15px;
  color: #8899a6;
}
.logout__buttons {
  border: none !important;
  background-color: var(--twitter-color) !important;
  border-radius: 30px !important;
  color: white !important;
  font-weight: 400 !important;
  text-transform: inherit !important;
  height: 40px !important;
  width: 30%;
  font-size: 15px !important;
  outline: none !important;
}
.cancel {
  background-color: #253341 !important;
}
.logout__buttons__div {
  display: flex;
  justify-content: space-evenly;
  margin-top: 20px;
  margin-bottom: 20px;
}

.MuiPopover-paper {
  background-color: var(--twitter-background) !important;
  color: white !important;
  box-shadow: rgba(136, 153, 166, 0.2) 0px 0px 15px,
    rgba(136, 153, 166, 0.15) 0px 0px 3px 1px !important;
}

.popup__head {
  display: flex;
  width: 100%;
  padding: 20px;
  border-bottom: 1px solid var(--twitter-border);
}

.popup__logout {
  padding: 20px;
}
.popup__logout:hover {
  background-color: var(--twitter-hover);
  cursor: pointer;
}
.bottom__info__popup {
  margin-left: 20px;
}
.end__popup {
  margin-left: 30px;
}

.sidebar {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: 100vh;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
.sidebar__main {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.sidebar__btn {
  border: none !important;
  border-radius: 30px !important;
  background-color: var(--twitter-color) !important;
  color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  height: 50px !important;
  width: 90%;
}
.sidebar__bottom {
  display: flex;
  padding: 15px;
  align-items: center;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 10px;
  flex: 1 1;
}
.bottom__info {
  margin-left: 20px;
}
.first {
  font-weight: 600;
}
.second {
  color: lightgray;
}
.sidebar__bottom:hover {
  background-color: var(--twitter-hover);
  transition: color 100ms ease-in-out;
  border-radius: 30px;
}
.end {
  margin-left: 50px;
}
.flexend {
  display: flex !important;

  margin-top: 30px;
}
.re {
  border-radius: 50%;
  background-color: var(--twitter-color);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: none;
}
.re > .MuiSvgIcon-root {
  padding: 10px;
  font-size: 55px;
}

@media only screen and (max-width: 1280px) {
  .bottom__info,
  .end {
    display: none;
  }
  .sidebar__btn {
    display: none !important;
  }
  .re {
    display: block;
  }
  .bottom {
    margin-left: auto !important;
  }
  .sidebar__main {
    align-items: center;
    padding: 0 5px;
  }
}

.MuiAlert-standardSuccess {
    background-color: var(--twitter-color) !important;
    color: white !important;
    font-size: 15px !important;
    font-weight: 500 !important;
}
.MuiAlert-icon{
    color:var(--twitter-background) !important
}
.tweetBox {
  padding-bottom: 10px;
  border-bottom: 8px solid var(--twitter-background);
  padding-right: 10px;
}
.tweetBox__input {
  padding: 20px;
  display: flex;
  
}
.tweetBox > form {
  display: flex;
  flex-direction: column;
}
.tweetBox__input > input {
  flex: 1 1;
  margin-left: 20px;
  font-size: 20px;
  border: none;
  outline: none;
  background-color: var(--twitter-background);
  color: white;
}
.tweetBox__imageInput {
  border: none;
  padding: 10px;
}
.tweetBox__tweetButton {
  background-color: var(--twitter-color) !important;
  border: none !important;
  color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  border-radius: 30px !important;
  width: 80px;
  height: 40px !important;
  margin-top: 20px !important;
  margin-left: auto !important;
}
.btn__dis{
  opacity: 0.6 !important;
}

.post {
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid var(--twitter-background);
  padding: 20px;
  color: white;
  border-bottom: 1px solid var(--twitter-border);
}
.post__body {
  margin-left: 20px;
  flex: 1 1;
}
.tp {
  height: 300px;
}
.tp > img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  margin-top: 10px;
}
.post__headerDescription {
  margin-top: 5px;
}

.post__footer {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
  color: rgb(112, 112, 112);
}
.post__headerSpecial {
  color: lightgray;
  font-size: 15px;
  font-weight: 200;
}
.post:hover {
  background-color: var(--twitter-hover);

  transition: color 100ms ease-in-out;
}
@media only screen and (max-width: 800px) {
  .tp {
    resize: both;
  }
}

.widgets {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  max-width: 360px;
  padding: 15px 20px;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.one {
  height: 300px;
  background-color: rgb(38, 56, 73);
  margin-top: 10px;
  display: flex;
  border-radius: 20px;
  flex-direction: column;
  justify-content: space-between;
}
.same1 {
  padding: 10px 15px;
  font-weight: 600;
  font-size: large;
}
.same2 {
  padding: 10px 15px;
  color: var(--twitter-color);
  background-color: rgb(38, 56, 73);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.same2:hover {
  background-color: var(--twitter-hover);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  transition: color 100ms ease-in-out;
}
.sidebar__search {
  display: flex;
  align-items: center;
  background-color: rgb(41, 61, 82);
  height: 39px;
  padding: 10px;
  border-radius: 9999px;
  margin-bottom: 15px;
}
.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: rgb(41, 61, 82);
  width: 100%;
  height: 30px;
}
.sidebar__searchContainer > input {
  border: none;
  margin-left: 10px;
  font-size: 20px;
  outline: none;
  font-family: "Montserrat", sans-serif;
  background-color: rgb(41, 61, 82);
  color: white !important;
  width: 100%;
}

.sidebar__searchContainer > .MuiSvgIcon-root {
  color: white;
  padding: 10px;
  font-size: 45px;
}
@media only screen and (max-width: 1180px) {
  .widgets {
    flex: 1 1;
  }
}

.feed__main {
  display: flex;
  flex: 1 1;
}
.feed__scroll {
  overflow-y: scroll;
  z-index: 200;
  color: white;
}
.feed__scroll::-webkit-scrollbar {
  display: none;
}

.feed__scroll {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.tweetbox {
  border-bottom: 10px solid var(--twitter-border);
}
.feed {
  min-width: 650px;
  max-width: 650px;
  border-right: 1px solid var(--twitter-border);
  border-left: 1px solid var(--twitter-border);
}
.widgets__main {
  max-width: 450px;
  padding: 0 20px;
}
.feed__header {
  z-index: 100;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: var(--twitter-background);
  color: white;
  border-bottom: 1px solid var(--twitter-border);
  padding: 15px 20px;
}

@media only screen and (max-width: 1080px) {
  .widgets__main {
    display: none;
  }
  .feed {
    flex: 0.9 1;
  }
}
@media only screen and (max-width: 800px) {
  .feed {
    flex: 1 1;
    min-width: 100px;
  }
}
@media only screen and (max-width: 1180px) {
}

.home {
  display: flex;
  max-width: 1400px;
  margin-right: auto;
  margin-left: auto;
  z-index: 0;
}
@media only screen and (max-width: 1280px) {
  .home {
    max-width: 1100px;
  }
}

@media only screen and (max-width: 1080px) {
  .home {
    max-width: 800px;
  }
}

.register {
  height: 100vh;
  display: grid;
  place-items: center;
}
.register__child {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  background-color: #192734;
  height: 85vh;
  width: 40vw;
  min-width: 600px;
  border-radius: 20px;
  box-shadow: -1px 20px 20px -6px rgba(0, 0, 0, 0.7);
}
.register__background {
  position: absolute;
  z-index: -2;
  height: 50%;
  width: 100%;
  background-color: var(--twitter-color);
}
.register__form {
  width: 70%;
}
.register__heading {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.register__heading > .MuiSvgIcon-root {
  padding: 10px;
  font-size: 70px;
  color: var(--twitter-color);
}
.register__container {
  height: 30px;
  margin-top: 40px;
}
.register__inputs {
  display: flex;
  flex-direction: column;
}
.register__container > input {
  width: 100%;
  border: none;
  margin-right: 15px;
  font-size: 15px;
  outline: none;
  font-family: "Montserrat", sans-serif;
  background-color: rgb(41, 61, 82);
  color: white !important;
  padding: 10px;
  border-radius: 20px;
}

.register__error {
  color: rgb(173, 31, 31);
  font-weight: bold;
}
.register__button {
  border: none !important;
  background-color: var(--twitter-color) !important;
  margin-top: 50px !important;
  border-radius: 30px !important;
  color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  height: 40px !important;
  width: 100%;
  font-size: larger !important;
  outline: none !important;
}
.login__link {
  font-size: 15px;
  font-weight: 500;
  color: var(--twitter-color);
  text-decoration: none !important;
}
.login__link__div {
  margin-top: 20px;
  text-align: center;
  margin-bottom: 50px !important;
}
.login__link:hover {
  color: rgb(64, 196, 240);
  text-decoration: none !important;
}

@media only screen and (max-width: 650px) {
  .register__child {
    min-width: 100%;
    height: 100vh;
    border-radius: 0;
    box-shadow: none;
  }
}

.login {
  display: flex;
  color: white;
  height: 100vh;
  position: relative;
}
.login__picture {
  flex: 0.6 1;
  height: 100%;
  background-color: #00afef;
  border-top-right-radius: 999px;
}
.login__picture > img {
  margin-top: 100px;
  margin-left: 50px;
}
.login__right {
  flex: 0.4 1;
  display: flex;
  flex-direction: column;
}

.login__error {
  color: red;
  font-size: 15px;
  margin-top: 10px;
  border-radius: 20px;
  padding: 10px;
  width: 60%;
  text-align: center;
  margin-right: auto;
}
.login__inputs {
  display: flex;
}
.di {
  display: none;
}
.login__text {
  margin-top: 70px;
}
.login__text__heading > h1 {
  font-size: 60px;
}
.login__register {
  font-size: 20px;
  font-weight: 500;
  color: var(--twitter-color);
  text-decoration: none;
}
.login__register:hover {
  color: rgb(64, 196, 240);
  text-decoration: none !important;
}
.login__register__div {
  margin-top: 70px;
}
.login__container {
  height: 30px;
  margin-top: 20px;
}
.login__container > input {
  border: none;
  margin-right: 15px;
  font-size: 15px;
  outline: none;
  font-family: "Montserrat", sans-serif;
  background-color: rgb(41, 61, 82);
  color: white !important;
  padding: 10px;
  border-radius: 20px;
}
.login__button {
  border: none !important;
  background-color: var(--twitter-color) !important;
  margin-top: 20px !important;
  border-radius: 30px !important;
  color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  height: 40px !important;
  outline: none !important;
}

@media only screen and (max-width: 1360px) {
  .login__inputs {
    flex-direction: column;
    justify-content: flex-start;
  }
  .login__container > input {
    width: 50%;
  }
  .login__text__heading > h1 {
    font-size: 50px;
  }
  .login__text {
    margin-top: 50px;
  }
  .login__register__div {
    margin-top: 50px;
  }
  .login__button {
    width: 50%;
  }
  .login__error {
    width: 50%;
  }
}

@media only screen and (max-width: 1220px) {
  .login__picture {
    display: none;
  }
  .login__right {
    flex: 0.6 1;
    margin-right: auto;
    margin-left: auto;
  }
  .login__text {
    text-align: center;
  }
.login__error{
  margin-left: auto;
}
  .login__container {
    align-items: center;
    text-align: center;
  }
  .login__container > input {
    margin-right: 0;
    min-width: 200px;
  }
  .login__btn {
    text-align: center;
  }
  .login__button {
    min-width: 200px !important;
  }
}
@media only screen and (max-width: 550px) {
  .login__text__heading > h1 {
    font-size: 30px;
  }
  .login__register {
    font-size: 15px;
  }
}

